//section1
.slogan-spacing{
    padding-top: 32px;
    padding-bottom: 52px;
    @include md{
        padding-top: 40px;
        padding-bottom: 80px;
    }
    @include lg {
        padding-top: 64px;
    }
}
.slogan_cards {
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: baseline;

    .slogan_card {
        max-width: 237px;
        @include lg{
            max-width: 208px;
        }
        .slogan_card_title {
            font-size: 20px;
            line-height: 28px;
            margin-top: 16px;
            @include lg {
                margin-top: 40px;
                font-size: 24px;
                line-height: 33px;
            }
        }
        .slogan_card_body {
            font-size: 16px;
            line-height: 24px;
            margin-top: 8px;
            @include lg {
                margin-top: 4px;
            }
        }
    }
    .slogan_card_order{
        @include lg {
            order: -1;
        }
    }
    
}

//section2
.classic_boxs {
    text-transform: uppercase;
    
    @include md{
        display: flex;
    }

    .classic_product {
    
        @include md{
            width: 57.2%;
            margin-right: 30px;
        }
    }

    .classic_product-sp {
        order: 0;

        @include lg {
            order: 1;
            margin-left: 30px;
            margin-right: 0;
        }
    }

    .classic_boxs_text {
        display: flex;
        align-items: center;

        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);

        color: white;

        @include md{
            color: $theme_primary;
            position: static;
            transform: translate(0,0);
        }
        
        .classic_text_img{
            width: 32px;
            height: 32px;
            background-size: contain;
            background-position: center center;
            background-repeat: no-repeat;
            @include lg{
                width: 48px;
                height: 48px;
            }
        }
        .classic_text_img_1{
            background-image: url('https://hexschool.github.io/webLayoutTraining1st/week3/icon-diamond-white.svg');
            @include md{
                background-image: url('https://hexschool.github.io/webLayoutTraining1st/week3/icon-diamond.svg');
            }
        }
        .classic_text_img_2{
            background-image: url('https://hexschool.github.io/webLayoutTraining1st/week3/icon-sun-white.svg');
            @include md{
                background-image: url('https://hexschool.github.io/webLayoutTraining1st/week3/icon-sun.svg');
            }
        }
        .classic_text_img_3{
            background-image: url('https://hexschool.github.io/webLayoutTraining1st/week3/icon-thunder-white.svg');
            @include md{
                background-image: url('https://hexschool.github.io/webLayoutTraining1st/week3/icon-thunder.svg');
            }
        }

        p {
            font-weight: 900;
            font-size: 28px;
            line-height: 37px;
            @include lg {
                font-size: 48px;
                line-height: 63px;
            }
        }
    }
}

.classic_flexend {
    justify-content: flex-start;

    @include lg {
        justify-content: flex-end;
    }
}

//section3
.coBranding {
    background-image: url('https://hexschool.github.io/webLayoutTraining1st/week3/bg.png');
    background-position: center center;

    .coBranding-boxs {
        @include md {
            display: flex;
        }

        .coBranding_box {
            position: relative;

            p {
                white-space: nowrap;
                font-size: 32px;
                line-height: 43px;
                font-weight: bold;
                position: absolute;
                top: 50%;
                left: 50%;
                z-index: 10;
                transform: translate(-50%, -50%);
                @include lg{
                    font-size: 72px;
                    line-height: 95px; 
                }
            }
        }
        .coBranding_box_spacing{
            margin-bottom: 16px;
            @include md {
                margin-bottom: 0;
                margin-right: 30px;
            }
        }

        .coBranding_img::before {
            content: '';
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            background-color: rgba(255, 255, 255, 0.5);
            z-index: 5;
        }
    }

}

//section4
.feedback_cards {
    width: 100%;
    justify-content: space-between;
    flex-wrap: wrap;

    .feedback_card {
        font-size: 14px;
        line-height: 19px;
        color: #212529;
        @include md {
            width: calc(50% - 15px);
        }
        @include lg {
            width: calc(25% - 22.5px);
        }
        .feedback_card_header{
            width: 100%;
            img{
                width: 100%;
            }
        }
        .feedback_card_body {
            // width: 100%;
            border: 1px solid #DEE2E6;
            padding: 16px;
            @include lg{
                padding: 14px 16px 15px 16px;
            }

            .feedback_card_body_content {
                font-size: 16px;
                line-height: 24px;
                margin: 16px 0;
                @include lg{
                    margin: 7px 0 10px 0; 
                }
            }
        }
    }
    .feedback_card_spacing{
        margin-bottom: 16px;
        @include md{
            margin-right: 30px;
            margin-bottom: 28px;
        }
        @include lg {
            margin-bottom: 0;
        }
    }
    .feedback_card_spacing-sp{
        margin-right: 0px;
        @include lg{
            margin-right: 30px;
        }
    }
}

//section5
.form_container {
    max-width: 345px;
    margin: 0 auto;
    @include md{
        max-width: 450px;
    }
    @include lg {
        max-width: 730px;
    }

    .form_subtitle {
        font-size: 16px;
        line-height: 24px;
        margin-bottom: 8px;
        @include md{
            margin-bottom: 12px;
        }
        @include lg{
            font-size: 20px;
            line-height: 30px;
            margin-bottom: 20px;
        }
    }
    form{
        font-size: 14px;
        line-height: 20px;
        @include lg{
            font-size: 16px;
            line-height: 22px;
        }
        label {
            display: block;
        }
        .formstyle{
            margin-bottom: 8px;
            @include lg{
                margin-bottom: 16px;
            }
        }
        .formstyle-sp{
            display: flex;
            margin-bottom: 24px;
            @include md{
                margin-bottom: 20px;
            }
            @include lg {
                margin-bottom: 42px;
            }
        }
        
        .form-mainstyle {
            width: 100%;
            border: 1px solid #000000;
            border-radius: 0px;
            text-indent: 16px;
            font-size: 14px;
            line-height: 20px;
            @include lg{
                font-size: 16px;
                line-height: 24px;
            }
        }
        .form_input-height {
            height: 50px;
        }
        textarea {
            height: 136px;
            padding-top: 14px;
        }
        .checkbox_text{
            font-size: 14px;
            line-height: 21px;
            @include lg{
                font-size: 16px;
                line-height: 24px;
            } 
        }
    }
    
    .btnstyle {
        display: flex;
        justify-content: center;

        .recommend_btn {
            cursor: pointer;
            width: 165px;
            height: 36px;
            border: 0;

            background-color:rgb(170,6,1);
            transition: 0.2s;
            &:hover{
                background-color: rgb(101,3,0);
                font-size: 16px;
            }
            @include md{
                width: 210px;
            }
            @include lg {
                width: 350px;
                height: 54px;
            }
        }
    }
}


