nav {
    height: 138px;
    background-color: $theme_primary;

    display: flex;
    flex-direction: column;
    align-items: center;


    @include md {
        height: 108px;
        padding: 0 39px;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
    }
    @include lg {
        padding: 0 88px;
    }


    h1 {
        height: 50px;
        display: flex;
        align-items: center;

        a {
            background-image: url('https://hexschool.github.io/webLayoutTraining1st/week3/logo.png');
            background-size: cover;
            background-position: center center;
            width: 48px;
            height: 29.14px;
            display: block;
            text-indent: 101%;
            overflow: hidden;
            white-space: nowrap;
            
            @include md {
                width: 80px;
                height: 48.57px;
            }
        }
    }


    .links {
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;


        font-size: 20px;
        line-height: 28px;
        @include md {
            width: 420px;
        }
        @include lg {
            width: 492px;
        }
        
        .link{
            border: 1px solid #fff;
            width: 50%;
            height: 44px;
            line-height: 44px;
            @include md {
                border: 0;
                width: auto;
            }
            a{
                display: block;
                &:hover {
                    color: $theme_primary;
                    background-color: #fff;
                }
            }
        }
    }
}

.banner {
    background-image: url('https://hexschool.github.io/webLayoutTraining1st/week3/banner-sm.png');
    background-size: cover;
    background-position: center center;
    width: 100%;
    height: 394px;
    @include md {
        
        height: 840px;
    }
    @include lg {
        background-image: url('https://hexschool.github.io/webLayoutTraining1st/week3/banner.png');
    }

    .banner-container {
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        justify-content: center;
        height: 100%;
        // border: 1px solid #000;

        p {
            font-size: 16px;
            line-height: 22px;
            @include md {
                font-size: 20px;
                line-height: 28px;
            }
            @include lg {
                font-size: 36px;
                line-height: 50px;
            }
        }

        h2 {
            font-size: 32px;
            line-height: 45px;
            @include md {
                font-size: 48px;
                line-height: 67px;
            }
            @include lg {
                font-size: 64px;
                line-height: 90px;
            }
        }
    }
}