
.footer-spacing{
    padding-top: 24px;
    padding-bottom: 25px;
    @include md{
      padding-top: 40px;
      padding-bottom: 40px;  
    }
    @include lg {
      padding-top: 48px;
      padding-bottom: 40px;  
    }  
  }
  .footer_top{
      display: flex;
      justify-content: space-between;
      border-bottom: 1px solid #fff;
  
      padding-bottom: 24px;
      @include md{
          padding-bottom: 27px;
      }
      @include lg {
          padding-bottom: 22px;
      }
      .footer_top_leftSide{
          .footer_top_leftSide_links{
              display: none;
              @include md{
                  display: flex;
                  margin-bottom: 24px;
              }
              @include lg {
                  margin-bottom: 36px;
              }
          }
          .leftSide_link-spacing{
              margin-right: 40px;
              @include lg{
                  margin-right: 48px;
              }
          }
          .footer_box{
              display: flex;
              align-items: center;
              font-size: 16px;
              line-height: 21px;
              @include md{
                  font-size: 20px;
                  line-height: 26px;
              }
              @include lg {
                  font-size: 36px;
                  line-height: 48px;
              }
              &:hover {
                  opacity: 0.6;
                  color: #ffffff;
                  font-weight: normal;
              }
              img{
                  width: 20px;
                  margin-right: 5px;
                  @include md {
                      width: 24px;
                      margin-right: 12px;
                  }
                  @include lg {
                      width: 44px;
                      margin-right: 16px;
                  }
              }
          }
          .footer_box_spacing{
              margin-bottom: 7px;
              @include md{
                  margin-bottom: 11px;
              }
              @include lg {
                  margin-bottom: 17px;
              }
          }
      }
    
      .footer_top_rightSide{
          display: flex;
          
          img{
              width: 24px;
              @include lg{
                  width: 48px;
              }
              &:hover {
                  filter: invert(100%);
              }
          }
          .rightSide_img-spacing{
              margin-right: 24px;
              @include lg{
                  margin-right: 32px;
              }
          }
      }  
  }
  .footer_bottom{
      padding-top: 18px;
      @include md{
          padding-top: 24px;
      }
      .footer_bot_items{
          // border: 1px solid #000;
          font-size: 14px;
          line-height: 20px;
          @include md{
              display: flex;
              align-items: center;
          }
          .footer_bot_item1{
              width: 46px;
              position: absolute;
              right: 0;
              bottom: 0;
              @include md{
                  display: none;
              }
              @include lg {
                  display: block;
                  margin-right: 38px;
  
                  width: 80px;
                  position: static;
              }
          }
          .footer_bot_item2{
              font-size: 14px;
              line-height: 19px;    
              @include md{
                  margin-right: auto;
                  font-size: 16px;
                  line-height: 21px;
              }
          }
          .footer_bot_item3{
              margin-top: 7px;
              @include md{
                  margin-top: 0;
                  margin-right: 24px;
              }
              @include lg {
                  margin-right: 62px;
              }
          }
          .footer_bot_item4{
              margin-top: 8px;
              @include md{
                  margin-top: 0;
              }
          }
      }
      
  }